import React, { useContext } from 'react';
import { StoreContext } from '../Store/StoreProvider';
import { Grid, Segment, Icon, Header, Divider, Card } from 'semantic-ui-react';
import CardGroupItems from '../components/CardGroupItems'


const HardSkillsSUI = () => {

    const  { aboutMe } = useContext(StoreContext);


    return (
<Segment vertical inverted color='black' className='segmentBoxShadow'>
        <Grid inverted stackable padded centered vertical >
  <Grid.Row>
            <Grid.Column widescreen={13}>
              <Grid.Row divided>
                <Header inverted as='h2'>
                  <Icon name={aboutMe.cardHeaderIcon} />
                  <Header.Content>{aboutMe.cardHeaderContent}</Header.Content>
                </Header> 
              </Grid.Row>
               <Grid.Row>
                <Divider inverted horizontal>Hard Skills</Divider>
                  <Card.Group centered stackable className='hardSkills'>     
                    <CardGroupItems CardItemsProps={aboutMe.cardItems} />
                  </Card.Group>  
              </Grid.Row>
            </Grid.Column>
            </Grid.Row>
            </Grid>
</Segment>
    )
}

export default HardSkillsSUI
